___CSS_LOADER_EXPORT___.push([module.id, "\n.checkbox-item[data-v-2c18f378]:hover {\r\n    background-color: rgba(0, 0, 0, 0.05);\n}\r\n", "",{"version":3,"sources":["webpack://./src/app/components/EasyMode.vue"],"names":[],"mappings":";AA2DA;IACI,qCAAqC;AACzC","sourcesContent":["<template>\r\n    <div class=\"box\">\r\n        <div class=\"mb-4\">\r\n            <h3 class=\"label is-size-5 mb-2\">Select Columns to Protect</h3>\r\n            <p class=\"is-size-7 has-text-grey\">\r\n                <span class=\"icon is-small has-text-info mr-1\">\r\n                    <i class=\"fas fa-info-circle\"></i>\r\n                </span>\r\n                Please choose the columns you want to protect and leave the rest unchecked.\r\n                <span class=\"has-text-weight-bold \">Note: Select numeric columns only.</span>\r\n            </p>\r\n        </div>\r\n\r\n        <div class=\" is-flex is-justify-content-space-between is-align-items-center\">\r\n\r\n            <div class=\"dropdown\" :class=\"{ 'is-active': isDropdownOpen }\">\r\n                <div class=\"dropdown-trigger\">\r\n                    <button class=\"button\" @click=\"isDropdownOpen = !isDropdownOpen\" aria-haspopup=\"true\">\r\n                        <span>{{ selectedHeadersPosition.length ? `${selectedHeadersPosition.length} Columns Selected` :\r\n                            'Select Columns' }}</span>\r\n                        <span class=\"icon is-small\">\r\n                            <i class=\"fas\" :class=\"isDropdownOpen ? 'fa-angle-up' : 'fa-angle-down'\"></i>\r\n                        </span>\r\n                    </button>\r\n                </div>\r\n                <div class=\"dropdown-menu\" id=\"dropdown-menu\" role=\"menu\" style=\"min-width: 200px;\">\r\n                    <div class=\"dropdown-content\">\r\n                        <div style=\"max-height: 200px; overflow-y: auto;\">\r\n                            <label v-for=\"(header, index) in allHeadersValues\" :key=\"index\"\r\n                                class=\"dropdown-item checkbox-item\" style=\"cursor: pointer;\">\r\n                                <input type=\"checkbox\" :value=\"index\" v-model=\"selectedHeadersPosition\" class=\"mr-2\">\r\n                                <span>{{ header }}</span>\r\n                            </label>\r\n                        </div>\r\n\r\n                        <hr class=\"dropdown-divider\">\r\n\r\n                        <div class=\"dropdown-item\">\r\n                            <button class=\"button is-primary is-fullwidth is-small\" @click=\"confirmSelection\"\r\n                                :disabled=\"selectedHeadersPosition.length === 0\">\r\n                                Done\r\n                            </button>\r\n                        </div>\r\n                    </div>\r\n                </div>\r\n            </div>\r\n\r\n            <button class=\"button is-success\" @click=\"protectSheet\" :disabled=\"selectedHeadersPosition.length === 0\">\r\n                <span class=\" icon is-small mr-1\"><i class=\"fas fa-lock\"></i></span>\r\n                <span>Protect Sheet</span>\r\n            </button>\r\n        </div>\r\n        <div id=\"status-feedback\" class=\"status mt-4\">\r\n            <StatusMessage :statusMessage=\"statusMessage\" />\r\n        </div>\r\n    </div>\r\n</template>\r\n\r\n<style scoped>\r\n.checkbox-item:hover {\r\n    background-color: rgba(0, 0, 0, 0.05);\r\n}\r\n</style>\r\n<script setup>\r\nimport { ref, onMounted, reactive } from 'vue';\r\nimport { useSecretRangeStore } from \"../stores/secret_ranges_store\";\r\nimport { SecretRange } from \"../models/secret_range\";\r\nimport ToolTip from \"../components/ToolTip.vue\";\r\nimport StatusMessage from \"../components/StatusMessage.vue\";\r\nimport { useStatus } from \"../composables/useStatus\";\r\nconst { statusMessage, setStatus } = useStatus();\r\nconst secretRangeStore = useSecretRangeStore();\r\n\r\nconst allHeadersValues = ref([]);//values / names of all headers\r\nconst isDropdownOpen = ref(false);\r\nconst selectedHeadersPosition = ref([]); //0 based indices\r\nconst contiguousIndices = ref([]);\r\nconst contiguousData = ref([]);\r\nconst contiguousDataHeaders = ref([]);\r\nconst contiguousIndicesHeaders = ref([]);\r\nconst lengthOfData = ref(0);\r\nconst widthOfData = ref(0);\r\nconst sheetName = ref(\"\");\r\nconst rangeObjects = reactive({\r\n    indices: [],\r\n    data: [],\r\n    indicesHeaders: [],\r\n    dataHeaders: []\r\n});\r\n\r\n\r\nconst confirmSelection = async () => {\r\n    isDropdownOpen.value = false;\r\n    fillContiguousArrays(selectedHeadersPosition.value, widthOfData.value, lengthOfData.value);\r\n    await fillRangeObjects(contiguousIndicesHeaders.value, \"indicesHeaders\");\r\n    await fillRangeObjects(contiguousDataHeaders.value, \"dataHeaders\");\r\n    await fillRangeObjects(contiguousIndices.value, \"indices\");\r\n    await fillRangeObjects(contiguousData.value, \"data\");\r\n}\r\n\r\nconst getDataInfo = () => {\r\n    Excel.run(async (context) => {\r\n        const sheet = context.workbook.worksheets.getActiveWorksheet();\r\n        const range = sheet.getUsedRange();\r\n        range.load(\"rowCount\");\r\n        range.load(\"values\");\r\n        range.load(\"columnCount\");\r\n        range.load(\"address\");\r\n        await context.sync();\r\n        allHeadersValues.value = range.values[0];\r\n        sheetName.value = range.address.split(\"!\")[0];\r\n        lengthOfData.value = range.rowCount;\r\n        widthOfData.value = range.columnCount;\r\n\r\n    })\r\n}\r\nonMounted(() => {\r\n    getDataInfo();\r\n\r\n});\r\n\r\nconst getSquashedRanges = (indices) => {\r\n    if (!indices || indices.length === 0) return [];\r\n\r\n    // Sort numerically to ensure correct grouping\r\n    indices.sort((a, b) => a - b);\r\n\r\n    const ranges = [];\r\n    let start = indices[0];\r\n    let prev = indices[0];\r\n\r\n    for (let i = 1; i < indices.length; i++) {\r\n        if (indices[i] !== prev + 1) {\r\n            // Gap found: Push the previous group\r\n            ranges.push({ start, end: prev });\r\n            start = indices[i];\r\n        }\r\n        prev = indices[i];\r\n    }\r\n    // Push the final group\r\n    ranges.push({ start, end: prev });\r\n\r\n    return ranges;\r\n}\r\n\r\nconst fillContiguousArrays = (selectedIndices, totalWidth, endRow) => {\r\n    // 1. Reset arrays\r\n    contiguousIndices.value = [];\r\n    contiguousData.value = []; // This will hold the INVERTED ranges\r\n\r\n    // --- PART A: Handle Selected Indices (contiguousIndices) ---\r\n    const selectedGroups = getSquashedRanges([...selectedIndices]); // Pass a copy\r\n\r\n    selectedGroups.forEach(group => {\r\n        const startLet = SecretRange.getColumnLetter(group.start);\r\n        const endLet = SecretRange.getColumnLetter(group.end);\r\n        // Format: Sheet1!A1:B1\r\n        contiguousDataHeaders.value.push(`${sheetName.value}!${startLet}${1}:${endLet}${1}`);\r\n        contiguousData.value.push(`${sheetName.value}!${startLet}${2}:${endLet}${endRow}`);\r\n    });\r\n\r\n    // --- PART B: Handle Inverted Indices (contiguousData) ---\r\n    // Create an array of ALL possible indices [0, 1, 2 ... width-1]\r\n    const allIndices = Array.from({ length: totalWidth }, (_, i) => i);\r\n\r\n    // Filter out the ones that are already selected\r\n    const invertedIndices = allIndices.filter(i => !selectedIndices.includes(i));\r\n\r\n    // Squash the inverted list\r\n    const invertedGroups = getSquashedRanges(invertedIndices);\r\n\r\n    invertedGroups.forEach(group => {\r\n        const startLet = SecretRange.getColumnLetter(group.start);\r\n        const endLet = SecretRange.getColumnLetter(group.end);\r\n        // Format: Sheet1!C1:D1\r\n        contiguousIndicesHeaders.value.push(`${sheetName.value}!${startLet}${1}:${endLet}${1}`);\r\n        contiguousIndices.value.push(`${sheetName.value}!${startLet}${2}:${endLet}${endRow}`);\r\n    });\r\n}\r\nconst fillRangeObjects = async (contiguousRanges, key) => {\r\n    Excel.run(async (context) => {\r\n        const sheet = context.workbook.worksheets.getActiveWorksheet();\r\n\r\n        for (const rangeStr of contiguousRanges) {\r\n            const range = sheet.getRange(rangeStr);\r\n            range.load(\"values\");\r\n            range.load(\"numberFormat\");\r\n            await context.sync();\r\n            const secret_range = new SecretRange(rangeStr, range.values, range.numberFormat);\r\n            rangeObjects[key].push(\r\n                secret_range\r\n            );\r\n        }\r\n    });\r\n}\r\nconst protectSheet = async () => {\r\n    try {\r\n        const pair = secretRangeStore.addPair(0,\r\n            \"EasyMode Pair\",\r\n            rangeObjects.indices,\r\n            rangeObjects.data,\r\n            () => {\r\n            },\r\n            (error) => {\r\n                clearSelections();\r\n                setStatus(\"Error protecting sheet: \" + error.message, 5000);\r\n            }\r\n        );\r\n        console.log(\"indices: \", rangeObjects.indices);\r\n        console.log(\"data: \", rangeObjects.data);\r\n        secretRangeStore.addHeaders(rangeObjects.indicesHeaders, rangeObjects.dataHeaders);\r\n        await pair.protectPair();\r\n        await secretRangeStore.createEncryptedDataSheet();\r\n        clearSelections();\r\n        secretRangeStore.clearPairs();\r\n        secretRangeStore.clearHeaders();\r\n        setStatus(\"Sheet protected successfully.\", 5000);\r\n    }\r\n    catch (e) {\r\n        console.error(\"Error in protectSheet:\", e);\r\n    }\r\n}\r\nconst clearSelections = () => {\r\n    selectedHeadersPosition.value = [];\r\n    contiguousIndices.value = [];\r\n    contiguousData.value = [];\r\n    contiguousDataHeaders.value = [];\r\n    contiguousIndicesHeaders.value = [];\r\n    rangeObjects.indices = [];\r\n    rangeObjects.data = [];\r\n    rangeObjects.indicesHeaders = [];\r\n    rangeObjects.dataHeaders = [];\r\n}\r\n</script>"],"sourceRoot":""}]);
