import { SecretRange } from "./secret_range";
import { encodeToBase64, generate256BitRandomBase64 } from "../../shared/utils"
import { writeSensitiveData } from "../../shared/apis";
import { useUserStore } from "../stores/user_store"
import { randomBytes } from 'crypto';
export default class SecretPair {
    id: number;
    label: String;
    indices: SecretRange[];
    data: SecretRange[];
    data_owner: string;
    protectedUniqueData: string[][] = [];
    // Excel Values Holders
    concatenated_data_values: number[][];
    concatenated_indices_values: string[][] = [];
    _encrypted_shares: Array<any> = [];
    _temp_generated_ids: string[][] = [];

    constructor(id: number, label: string, data: SecretRange[], indices: SecretRange[]) {
        // TODO: will need to remove this later and support multiple data cols
        this.id = id;
        this.label = label;
        this.data = data;
        this.indices = indices;
        this.data_owner = useUserStore().currentUser as string;
        this.concatenated_indices_values = SecretRange.concatenateSecretRangesValues(this.indices);
        this.concatenated_data_values = SecretRange.concatenateSecretRangesValues(this.data);
        this.checkDataTypes();
    }


    async protectPair() {
        const columnCount = this.concatenated_data_values[0]?.length || 0;
        this._temp_generated_ids = this.concatenated_data_values.map(row =>
            row.map(() => generate256BitRandomBase64())
        );
        for (let col = 0; col < columnCount; col++) {
            const colData = this.concatenated_data_values.map(row => row[col]);
            const colIndices = this._temp_generated_ids.map(row => row[col]);
            const requestPayload = {
                user: this.data_owner,
                data: colData,
                indices: colIndices
                //FIXME fill this later, this need to be UUID, and store that to use later in the output sheet
                //indices: this.concatenated_indices_values.map((row) => encodeToBase64(row.join("|"))).flat()
            };
            const resp = await writeSensitiveData(requestPayload)
            this._encrypted_shares.push(resp.encryptedShares)
        }
        const concatenatedColumns = this.concatenateShares(this._encrypted_shares);
        const rawProtectedRows = this.transpose(concatenatedColumns);
        this.protectedUniqueData = rawProtectedRows.map((row, rowIndex) => {
            return row.map((shareValue, colIndex) => {
                const originalID = this._temp_generated_ids[rowIndex][colIndex];
                return `${originalID} ? ${shareValue}`;
            });
        });
        this.distributeProtectedDataToRanges();
        this._temp_generated_ids = [];
    }

    concatenateShares(encrypted_shares: string[][]): string[][] {
        const results: string[][] = [];
        const SHARES_PER_DATA_CELL = 3;
        for (let rowIndex = 0; rowIndex < encrypted_shares.length; rowIndex++) {
            const concatenatedRow: string[] = [];

            for (let colIndex = 0; colIndex < encrypted_shares[rowIndex].length; colIndex += SHARES_PER_DATA_CELL) {
                const shareGroup = encrypted_shares[rowIndex].slice(colIndex, colIndex + SHARES_PER_DATA_CELL);
                const concatenatedShare = shareGroup.join("|");
                concatenatedRow.push(concatenatedShare); // Placeholder for unique protected data
            }
            results.push(concatenatedRow);
        }
        return results;
    }

    transpose(matrix: string[][]): string[][] {
        if (!matrix.length) return [];
        // we use matrix[0] to know the number of columns in the original matrix
        return matrix[0].map((_, colIndex) => {
            // we iterate over each row, get the element at colIndex and form a new row
            return matrix.map(row => row[colIndex]);
        });
    }



    clearArrays(): void {
        this.concatenated_indices_values = [];
        this.concatenated_data_values = [];
        this._encrypted_shares = [];
    }

    // this function is used to distribute back the protected data with its original dimensions
    private distributeProtectedDataToRanges() {
        let currentCol = 0;

        for (const dataRange of this.data) {
            const rangeHeight = dataRange.endRow - dataRange.startRow + 1;
            const rangeWidth = dataRange.endCol - dataRange.startCol + 1;

            dataRange.protectedValues = [];

            // Extract each row for this range
            for (let r = 0; r < rangeHeight; r++) {
                // Slice the columns that belong to this range
                const protectedRow = this.protectedUniqueData[r].slice(
                    currentCol,
                    currentCol + rangeWidth
                );
                dataRange.protectedValues.push(protectedRow);
            }

            currentCol += rangeWidth; // Move to the next range's columns
        }
    }

    checkDataTypes() {
        for (const col of this.concatenated_data_values) {
            for (const val of col) {
                if (typeof val !== 'number') {
                    this.concatenated_data_values = [];
                    throw new Error(`Data range must contain only numeric values, but found value: ${val}`);
                }
            }
        }
    }
}
