<template>
    <div v-if="statusMessage" class="notification is-light mb-4" :class="{
        'is-success': statusMessage.includes('success'),
        'is-danger': statusMessage.includes('Error') || statusMessage.includes('error'),
        'is-info': !statusMessage.includes('success') && !statusMessage.includes('Error') && !statusMessage.includes('error'),
    }">
        <div class="is-flex is-align-items-center">
            <span class="icon is-medium mr-3">
                <i v-if="statusMessage.includes('success')" class="fas fa-check-circle fa-lg"></i>
                <i v-else-if="statusMessage.includes('Error') || statusMessage.includes('error')"
                    class="fas fa-exclamation-triangle fa-lg"></i>
                <i v-else class="fas fa-info-circle fa-lg"></i>
            </span>

            <span class="has-text-weight-medium">
                {{ statusMessage }}
            </span>
        </div>
    </div>
</template>

<script setup>
const props = defineProps({
    statusMessage: {
        type: String,
        required: false,
        default: "",
    },
});


</script>