<template>
    <div class="field">
        <label class="label">{{ props.label }}
            <ToolTip v-if="props.helpText" :helpText="props.helpText" />
        </label>
        <div class="field has-addons">
            <p class="control is-expanded has-icons-right">
                <input class="input" type="text" placeholder="e.g., D2:F50" v-model="range" @focus="handleFocus"
                    @blur="handleBlur" />
                <span class="icon is-small is-right" v-if="range" @click="props.clearHandler"
                    style="pointer-events: auto; cursor: pointer;">
                    <i class="fas fa-times"></i>
                </span>
            </p>

            <p class="control">
                <button class="button is-info" @click="props.rangeSelectorHandler">{{ props.buttonText }}</button>
            </p>
        </div>
    </div>
</template>

<script setup>
import { computed, ref } from "vue";
import ToolTip from "../components/ToolTip.vue";

const props = defineProps({
    label: {
        type: String,
        required: true,
    },
    helpText: {
        type: String,
        required: false,
    },
    buttonText: {
        type: String,
        required: true,
    },
    rangeSelectorHandler: {
        type: Function,
        required: true,
    },
    clearHandler: {
        type: Function,
        required: true,
    },
    modelValue: {
        type: String,
        required: true,
    },

});
const emit = defineEmits(['update:modelValue', 'change']);
const valueOnFocus = ref("");

const range = computed({
    get() {
        const fullRangeAddress = props.modelValue;
        // get clean address without the sheet name Sheet1!A1:b1 => A1:B1
        return fullRangeAddress.split("!").pop();
    },
    set(value) {
        emit('update:modelValue', value);
    }
});

const handleFocus = () => {
    valueOnFocus.value = range.value;
};
const handleBlur = () => {
    if (range.value && range.value !== valueOnFocus.value) {
        emit('change', range.value);
    }
}
</script>
