const ColorPalette = {
    // From Option 1: Standard Blue
    BASE: {
        INDICES: "#93C5FD", // (Tailwind Blue-300)
        DATA: "#E0F2FE",    // (Tailwind Sky-100)
    },

    // From Option 3: Dark Teal
    HEADERS: {
        // Dark, rich teal header (assumes light/white text)
        INDICES: "#0F766E", // (Tailwind Teal-700)
        DATA: "#2EAC9C", // Teal-300
    },

    // From Option 1: Standard Blue
    HIGHLIGHT: {
        INDICES: "#60A5FA", // (Tailwind Blue-400)
        DATA: "#BFDBFE",    // (Tailwind Blue-200)
    },

    // From Option 1: Standard Red
    ALERT: {
        ERROR: "#FCA5A5",   // (Tailwind Red-300)
    },

    NO_FILL: null,
};
/**
 * Processes encrypted shares from the server into a structured format.
 * Groups shares into chunks of 3 and formats them for Excel output.
 * @param encryptedShares - Flat array of encrypted share strings from the server
 * @param FormattedIndices - Original index values from the sheet after the join operation. 
 * @param username - Data owner's username
 * @returns 2D array formatted for Excel worksheet
 * @throws Error if share count is invalid
 */
function formatEncryptedShares(encryptedShares: string[][], FormattedIndices: any[][]): string[][] {
    const SHARES_PER_ROW = 3;
    const results: string[][] = [];
    const tempFormattedIndices = FormattedIndices.map((row) => row.join("|")).flat();

    console.log("Formatted indices", FormattedIndices);
    console.log("encrypted shares", encryptedShares);

    const numRows = tempFormattedIndices.length / encryptedShares.length; // rows per column

    for (let colIndex = 0; colIndex < encryptedShares.length; colIndex++) {
        const columnShares = encryptedShares[colIndex];
        const concatenatedShares = [];

        for (let i = 0; i < columnShares.length; i += SHARES_PER_ROW) {
            const shareGroup = columnShares.slice(i, i + SHARES_PER_ROW);
            concatenatedShares.push(shareGroup.join("|"));
        }

        // Only iterate through the indices for THIS column
        const startIdx = colIndex * numRows;
        const endIdx = startIdx + numRows;

        for (let i = startIdx; i < endIdx; i++) {
            const row = [tempFormattedIndices[i], concatenatedShares[i - startIdx]];
            console.log(row);
            results.push(row);
        }
    }

    return results;
}

function colorExcelRange(context: Excel.RequestContext, address: string, color: string | null): void {
    // Only attempt to format if an address has been set
    if (!address) { return; }

    try {
        const addressParts = address.split("!");
        const relativeAddress = addressParts.pop();
        const range = context.workbook.worksheets.getActiveWorksheet().getRange(relativeAddress);

        console.log(`Coloring for range: ${relativeAddress} with color: ${color}`);
        console.log("color value: ", color);

        if (color === ColorPalette.NO_FILL || color == null) {
            console.log("NO FILL condition met");
            console.log(`Clearing fill color for range: ${relativeAddress}`);
            range.format.fill.clear();
        } else {
            range.format.fill.color = color;
        }
    } catch (error) {
        // This can fail if the sheet was deleted or renamed.
        console.warn(`Could not color for range ${address}: ${error.message}`);
    }
}

