async function restoreOriginalColors(rangeAddress) {
    await Excel.run(async (context) => {
        const sheet = context.workbook.worksheets.getActiveWorksheet();
        const range = sheet.getRange(rangeAddress);

        // CLEAR the conditional format. 
        // The original underlying color (whatever it was) instantly reveals itself.
        range.conditionalFormats.clearAll();

        await context.sync();
    });
}














/**
 * Gets the background fill color of a cell.
 * @param context The Excel RequestContext.
 * @param address The cell address (e.g., "A1" or "Sheet1!A1").
 * @returns A promise that resolves to the color (e.g., "#FFFFFF") or null if the cell has no fill.
 */
async function getCellColor(context: Excel.RequestContext, address: string): Promise<string | null> {
    try {
        // 1. Get the range from the address.
        // This handles "A1" (active sheet) or "Sheet1!A1" (specific sheet).
        const range = context.workbook.worksheets.getActiveWorksheet().getRange(address);

        // 2. Get the top-left cell, in case a multi-cell range was given.
        const cell = range.getCell(0, 0);

        // 3. Load the specific property we want to read from Excel.
        cell.load("format/fill/color");

        // 4. Synchronize with Excel to fetch the loaded property.
        await context.sync();

        // 5. Return the color. This will be a hex string (e.g., "#FF0000").
        return cell.format.fill.color;

    } catch (error) {
        // 6. Handle errors. A common error is "ItemNotFound" which
        //    Excel throws if you try to read the color of a "No Fill" cell.
        if (error instanceof OfficeExtension.Error && error.code === "ItemNotFound") {
            // This means the fill property doesn't exist, i.e., "No Fill"
            console.log(`Cell ${address} has no fill.`);
            return null; // Return null to represent "No Fill"
        }

        // Handle other potential errors (e.g., invalid address)
        console.warn(`Could not get color for cell ${address}: ${error.message}`);
        throw error; // Re-throw other errors
    }
}



/**
 * Converts a string to Base64-encoded UTF-8 bytes.
 * @param value - The string to encode
 * @returns Base64-encoded string
 */
function encodeToBase64(value: string): any {
    const utf8Bytes = new TextEncoder().encode(value);
    const binaryString = String.fromCharCode(...Array.from(utf8Bytes));
    //btoa takes binary strings and encodes them to Base64 ASCII
    return btoa(binaryString);
}
function generate256BitRandomBase64(): string {
    const randomBytes = new Uint8Array(32);
    crypto.getRandomValues(randomBytes);

    return btoa(Array.from(randomBytes, byte => String.fromCharCode(byte)).join(''));
}


