<template>
    <!-- Headers Selection  //TODO: add explanatory text to the helpText fields-->
    <div v-if="!hasHeaders">
        <div class="mb-4">
            <label class="label is-size-5 mb-2">Step 1: Define Your Table Structure</label>
            <p class="has-text-grey is-size-7 mb-3">
                Tell us which columns contain labels and which contain sensitive data. This is a one-time setup.
            </p>
        </div>
        <div class="box">

            <span class="tag is-info is-light">
                <span class="icon is-small mr-1"><i class="fas fa-eye"></i></span>
                <span>Stays Visible</span>
            </span>
            <SelectRange label="Label Columns Headers"
                helpText="Select the header row cells that identify and describe your data rows. These columns will remain visible in the protected sheet."
                buttonText="Select Headers" :rangeSelectorHandler="selectIndicesHeaders"
                :clearHandler="() => clearRange('indicesHeaders')" v-model="ranges.indicesHeaders"
                @change="selectIndicesHeaders" class="mt-3" />

            <hr class="dropdown-divider my-4">

            <span class="tag is-warning is-light">
                <span class="icon is-small mr-1"><i class="fas fa-lock"></i></span>
                <span>Gets Protected</span>
            </span>
            <SelectRange label="Sensitive Data Headers" buttonText="Select Headers"
                helpText="Select the header row cells containing sensitive numeric values that need protection. These columns will be encrypted in the protected sheet."
                :rangeSelectorHandler="selectDataHeaders" :clearHandler="() => clearRange('dataHeaders')"
                v-model="ranges.dataHeaders" @change="selectDataHeaders" class="mt-4" />

            <div class="field mt-5">
                <p class="control">
                    <button class="button is-primary is-fullwidth" :disabled="!canSaveHeaders" @click="saveHeaders">
                        <span>Save Configuration</span>
                    </button>
                </p>
            </div>
        </div>
    </div>
    <div v-else>
        <div class="mb-4">
            <label class="label is-size-5 mb-2">Step 2: Select Your Data</label>
            <p class="has-text-grey is-size-7 mb-3">
                Choose the data rows you want to protect. You can select multiple ranges if needed.
            </p>
        </div>

        <div class="box">
            <!-- select indices ranges -->
            <div class="is-light is-success is-small mb-4">
                <div class="is-flex is-justify-content-space-between is-align-items-center">
                    <div class="is-flex is-align-items-center">
                        <span class="icon has-text-success mr-2">
                            <i class="fas fa-check-circle"></i>
                        </span>
                        <div>
                            <p class="has-text-weight-semibold is-size-7">Headers Configured</p>
                            <p class="is-size-7 has-text-grey-dark">
                                Header ranges are highlighted in your spreadsheet
                            </p>
                        </div>
                    </div>
                    <button class="button is-small is-outlined is-success" @click="editHeaders">
                        <span class="icon is-small">
                            <i class="fas fa-edit"></i>
                        </span>
                        <span>Edit</span>
                    </button>
                </div>
            </div>



            <SelectRange label="Label Data Range" buttonText="Select Range" :rangeSelectorHandler="selectIndices"
                helpText="Select the rows of data under your Label Columns headers. You can select multiple ranges if your data is not continuous."
                :clearHandler="() => clearRange('indices')" @change="selectIndices" v-model="ranges.indices" />

            <!-- select data ranges -->
            <SelectRange label="Sensitive Data Range" buttonText="Select Range" :rangeSelectorHandler="selectData"
                helpText="Select the rows of sensitive numeric data to protect. Must correspond to the same rows selected in Label Data Range."
                :clearHandler="() => clearRange('data')" @change="selectData" v-model="ranges.data" class="mt-4" />

            <div class="field mt-5">
                <p class="control">
                    <button class="button is-primary is-fullwidth" :disabled="!canCreateRange" @click="saveRange">Create
                        Secure Range</button>
                </p>
            </div>
        </div>
    </div>

</template>

<script setup>
import { ref, computed, reactive } from "vue";
import SelectRange from "../components/SelectRange.vue";
import { SecretRange } from "../models/secret_range";
import { useSecretRangeStore } from "../stores/secret_ranges_store";
import { useStatus } from "../composables/useStatus";
import { colorExcelRange, getCellColor, ColorPalette } from "../../shared/utils";
import { Headers } from "../models/headers";

const secretRangeStore = useSecretRangeStore();
const { setStatus } = useStatus();

// Range text references (for display)
const ranges = reactive({
    indices: "",
    data: "",
    indicesHeaders: "",
    dataHeaders: ""
});

// Range objects (for validation and storage)
const rangeObjects = reactive({
    indices: [],
    data: [],
    indicesHeaders: [],
    dataHeaders: []
});


const headers = ref({}); // the full header object after creation type() => Headers
const hasHeaders = computed(() => secretRangeStore.headers !== null);
const canSaveHeaders = computed(() => ranges.indicesHeaders && ranges.dataHeaders);
const canCreateRange = computed(() => ranges.indices && ranges.data);


const validateAndStoreRange = (rangeAddress, rangeValues, rangeKey, validateRange) => {
    console.log("range address ", rangeAddress)
    const rangeObject = new SecretRange(rangeAddress, rangeValues);
    validateRange(rangeObject);
    ranges[rangeKey] = rangeAddress;
    rangeObjects[rangeKey].push(rangeObject);
};


const createRangeSelector = (rangeKey, color, successMessage, errorMessage, validateRange) => {
    return async (providedValue) => {
        try {
            await Excel.run(async (context) => {
                let rangeAddress;
                let selectedRanges;
                if (providedValue && typeof providedValue === 'string') {
                    const sheet = context.workbook.worksheets.getActiveWorksheet();
                    const manualRange = sheet.getRange(providedValue);
                    manualRange.select();
                    manualRange.load("address");
                    await context.sync();
                    rangeAddress = manualRange.address;
                }
                else {
                    rangeAddress = context.workbook.getSelectedRanges();
                }
                const areas = rangeAddress.areas;
                areas.load("address, values");
                await context.sync();
                areas.items.forEach((range) => {
                    validateAndStoreRange(range.address, range.values, rangeKey, validateRange);
                });
                console.log("stored range object ", rangeObjects[rangeKey]);
                colorExcelRange(context, rangeAddress, color);
                setStatus(successMessage, 3000);

            });
        } catch (error) {
            clearRange(rangeKey);
            setStatus(errorMessage + ": " + error.message, 4000);
            console.error(error);
        }
    };
};

const validateIndices = (rangeObject) => {
    headers.value.validateIndicesWithinHeaders(rangeObject);
    if (rangeObjects.data) {
        SecretRange.validateRangesAlignment(rangeObjects.data, rangeObject);
    };
}

const validateData = (rangeObject) => {
    headers.value.validateDataWithinHeaders(rangeObject);
    if (rangeObjects.indices) {
        SecretRange.validateRangesAlignment(rangeObjects.indices, rangeObject);
    };
}

const validateIndicesHeaders = (rangeObject) => {
    Headers.validateHeader(rangeObject);
    if (rangeObjects.dataHeaders) {
        SecretRange.validateRangesAlignment(rangeObjects.dataHeaders, rangeObject);
        SecretRange.validateNonDuplicatedRange(rangeObjects.dataHeaders, rangeObject);
        SecretRange.validateRangesNotOverlapping(rangeObjects.dataHeaders, rangeObject);
    };
}


const validateDataHeaders = (rangeObject) => {
    Headers.validateHeader(rangeObject);
    if (rangeObjects.indicesHeaders) {
        SecretRange.validateRangesAlignment(rangeObjects.indicesHeaders, rangeObject);
        SecretRange.validateNonDuplicatedRange(rangeObjects.indicesHeaders, rangeObject);
        SecretRange.validateRangesNotOverlapping(rangeObjects.indicesHeaders, rangeObject);
    };
}

const selectIndices = createRangeSelector(
    "indices",
    ColorPalette.BASE.INDICES,
    "Selected Labels",
    "Error selecting Labels",
    validateIndices
);
const selectData = createRangeSelector(
    "data",
    ColorPalette.BASE.DATA,
    "Selected Sensitive Data",
    "Error selecting data ",
    validateData
);

// TODO: Pick another color scheme for headers
const selectIndicesHeaders = createRangeSelector(
    "indicesHeaders",
    ColorPalette.HEADERS.INDICES,
    "Selected Labels Headers",
    "Error selecting labels headers",
    validateIndicesHeaders
);

const selectDataHeaders = createRangeSelector(
    "dataHeaders",
    ColorPalette.HEADERS.DATA,
    "Selected Sensitive Data Headers",
    "Error selecting sensitive data headers",
    validateDataHeaders
);

const saveRange = () => {
    const id = secretRangeStore.nextId;
    const label = "Pair" + String(secretRangeStore.nextId);

    const onSuccess = () => {
        setStatus("Secure range created successfully!", 3000);
        clearForm();
    };

    const onFailure = async (error, indices, data) => {
        Excel.run(async (context) => {
            // remove colored ranges
            // TODO: reset color to it's original form not just no fill
            colorExcelRange(context, ranges.indices, ColorPalette.NO_FILL);
            colorExcelRange(context, ranges.data, ColorPalette.NO_FILL);
            await context.sync();
            setStatus("Error creating secure range: " + error.message, 5000);
            clearForm();
        });
    };

    secretRangeStore.addPair(
        id,
        label,
        rangeObjects.indices,
        rangeObjects.data,
        onSuccess,
        onFailure
    );

};
const saveHeaders = () => {
    try {
        headers.value = secretRangeStore.addHeaders(rangeObjects.indicesHeaders, rangeObjects.dataHeaders);
        setStatus("Headers saved successfully!", 3000);
    }
    catch (error) {
        setStatus("Error selecting headers: " + error.message, 4000);
    }
};

const clearForm = () => {
    ranges.indices = "";
    ranges.data = "";
    rangeObjects.indices = [];
    rangeObjects.data = [];
}

const editHeaders = () => {
    secretRangeStore.clearHeaders();
    ranges.indicesHeaders = "";
    ranges.dataHeaders = "";
}

const clearRange = (rangeKey) => {
    ranges[rangeKey] = "";
    rangeObjects[rangeKey] = [];
};


</script>
