// src/shared/api.ts

// Add this helper function at the top of api.ts
function getBaseUrl(): string {
    // Check the query parameters of the current window (the task pane)
    const urlParams = new URLSearchParams(window.location.search);
    const useLocal = urlParams.get('server') === 'local';
    const env = 'staging';
    const urls = {
        development: 'https://localhost:8080',
        staging: 'https://webrpc.staging.nera.systems',
        production: 'https://webrpc.nera.systems'
    }
    if (useLocal) {
        console.log('Build configured to use LOCALHOST');
        return 'https://localhost:8080';
    } else {
        if (env in urls) {
            console.log(`Build configured to use ${env.toUpperCase()} server`);
            return urls[env as keyof typeof urls];
        }
        throw new Error(`Unknown environment configuration: ${env}`);
    }
}

/**
 * A helper function to generate all keys for a user.
 * @param user The username.
 */
async function generateKeys(user: string): Promise<any> {
    // const fullUrl = 'https://webrpc.nera.systems/rpc/GenerateKeys';
    // const fullUrl = 'https://webrpc.staging.nera.systems/rpc/GenerateKeys';
    // const fullUrl = 'https://localhost:8080/rpc/GenerateKeys';
    const baseUrl = getBaseUrl();
    const fullUrl = `${baseUrl}/rpc/GenerateKeys`;

    const payloadObject = {
        payload: {
            session_id: user,
            user: user
        }
    };

    const options = {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json',
        },
        body: JSON.stringify(payloadObject)
    };

    console.log(`Generating keys for user: ${user} from ${fullUrl}`);
    const response = await fetch(fullUrl, options);

    if (!response.ok) {
        const errorBody = await response.json();
        throw new Error(`Failed to generate keys for user ${user}: ${JSON.stringify(errorBody)}`);
    }

    return await response.json();
}

/**
 * A helper function to public key sfor a user.
 * @param user The username.
 */
async function publishKeys(user: string): Promise<any> {
    // const fullUrl = 'https://webrpc.nera.systems/rpc/GenerateKeys';
    // const fullUrl = 'https://webrpc.staging.nera.systems/rpc/GenerateKeys';
    // const fullUrl = 'https://localhost:8080/rpc/GenerateKeys';
    const baseUrl = getBaseUrl();
    const fullUrl = `${baseUrl}/rpc/PublishKeys`;

    const payloadObject = {
        payload: {
            session_id: user,
            user: user
        }
    };

    const options = {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json',
        },
        body: JSON.stringify(payloadObject)
    };

    console.log(`Publishing keys for user: ${user} from ${fullUrl}`);
    const response = await fetch(fullUrl, options);

    if (!response.ok) {
        const errorBody = await response.json();
        throw new Error(`Failed to publish keys for user ${user}: ${JSON.stringify(errorBody)}`);
    }

    return await response.json();
}

/**
 * Orchestrates the generation and publishing of keys for a user.
 * * NOTE: Unlike the previous version, this runs SEQUENTIALLY.
 * We must wait for generation to finish before we can publish.
 * * @param user The username.
 * @param onSuccess Callback when a step finishes successfully. Receives the step name ('Generate' | 'Publish').
 * @param onFailure Callback when a step fails. Receives the step name and the error.
 */
export async function generateAndPublishKeysForUser(
    user: string,
    onSuccess: (step: string) => void,
    onFailure: (step: string, error: any) => void
): Promise<void> {

    // --- Step 1: Generate Keys ---
    try {
        await generateKeys(user);
        // We wrap this in an arrow function to ensure it doesn't execute immediately
        if (onSuccess) onSuccess('generate');
    } catch (error) {
        console.error(`Error generating keys for ${user}`, error);
        if (onFailure) onFailure('generate', error);
        return; // specific logic: if generation fails, do not attempt to publish
    }

    // --- Step 2: Publish Keys ---
    try {
        await publishKeys(user);
        if (onSuccess) onSuccess('publish');
    } catch (error) {
        console.error(`Error publishing keys for ${user}`, error);
        if (onFailure) onFailure('publish', error);
    }

    console.log(`Key orchestration finished for user: ${user}`);
}

/**
 * Sends the clear data and indices to the server to be converted into secret shares.
 * @param payload The object containing the user, data, and indices.
 * @returns The server's response, which should contain the encrypted data.
 */
export async function writeSensitiveData(payload: { user: string, data: number[], indices: string[] }): Promise<any> {
    // const fullUrl = 'https://webrpc.nera.systems/rpc/WriteSensitiveData';
    // const fullUrl = 'https://webrpc.staging.nera.systems/rpc/WriteSensitiveData';
    // const fullUrl = 'https://localhost:8080/rpc/WriteSensitiveData';
    const baseUrl = getBaseUrl();
    const fullUrl = `${baseUrl}/rpc/WriteSensitiveData`;

    // The payload for the body is the entire object, including the "payload" key
    const bodyPayload = {
        payload: payload
    };
    const options = {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json',
        },
        body: JSON.stringify(bodyPayload)
    };

    console.log(`Sending sensitive data to the server ${fullUrl}...`);
    const response = await fetch(fullUrl, options);
    console.log("Received response from server.");
    console.log("Response status:", response.status);

    if (!response.ok) {
        const errorBody = await response.json();
        throw new Error(`API Error from WriteSensitiveData: ${JSON.stringify(errorBody)}`);
    }

    console.log("Server responded successfully.");
    return await response.json();
}