import { defineStore } from 'pinia';
import { ref, use } from 'vue';
import { SecretRange } from '../models/secret_range';
import SecretPair from '../models/secret_pair';
import { useUserStore } from "../stores/user_store"
import { Headers } from '../models/headers';
import ExcelJS from 'exceljs';

export const useSecretRangeStore = defineStore('secretRanges', () => {
  const nextId = ref(0);
  const pairs = ref([]);  // Array of SecretPair objects
  const headers = ref<Headers | null>(null); // Headers object



  function addHeaders(indicesRanges: SecretRange[], dataRanges: SecretRange[]) {
    headers.value = new Headers(indicesRanges, dataRanges);
    //headers.value.fillHeadersValues(indicesRange, dataRange);

    return headers.value;
  }


  function clearHeaders() {
    headers.value = null;
  }

  function addPair(id: number, label: string, indices: SecretRange[], data: SecretRange[], onSuccess: Function, onFailure: Function) {
    try {
      // get the value of the data header to later append to the indices rows in the protected sheet
      const x = new SecretPair(id, label, data, indices);
      pairs.value.push(x);
      nextId.value += 1;
      onSuccess(x)
      return x;
    }
    catch (e) {
      onFailure(e, indices, data);
      throw e;
    }
  }

  function deletePair(id: number) {
    // this is used because splice modifies the array in place and shifts indices after deletion
    const index = pairs.value.findIndex(pair => pair.id === id);
    if (index === -1) {
      throw new Error(`Pair with id ${id} not found`);
    }
    const deletedPair = pairs.value.splice(index, 1);

    return deletedPair;
  }


  function writeHeaders(sheet: ExcelJS.Worksheet, headers: Headers) {
    const START_ROW = 1;
    // const START_COL = 1;
    const indicesHeaders = headers?.concatenated_indices_headers_values[0] || [];
    const dataHeaders = headers?.concatenated_data_headers_values[0] || [];


    const allHeaders = [...indicesHeaders, ...dataHeaders];

    // Write everything in one line
    sheet.getRow(START_ROW).values = allHeaders;
  }

  function writeMatrix(sheet: ExcelJS.Worksheet, startRow: number, startCol: number, secretRange: SecretRange, useProtected: boolean = false) {
    const data = useProtected ? secretRange.protectedValues : secretRange.values;

    if (!data) return;

    for (let r = 0; r < data.length; r++) {
      for (let c = 0; c < data[0].length; c++) {
        const cell = sheet.getCell(startRow + r, startCol + c);
        cell.value = data[r][c];

        if (secretRange.numberFormat && secretRange.numberFormat[r] && secretRange.numberFormat[r][c]) {
          cell.numFmt = secretRange.numberFormat[r][c];
        }
      }
    }
  }
  function clearPairs() {
    pairs.value = [];
    nextId.value = 0;
  }

  async function createEncryptedDataSheet() {
    // 1. SETUP: Create a "Virtual" Workbook in memory
    const workbook = new ExcelJS.Workbook();
    const sheet = workbook.addWorksheet("Protected Data Sheet");

    // 2. PREPARE HEADERS
    const indicesHeaders = headers.value?.concatenated_indices_headers_values[0] || [];
    const dataHeaders = headers.value?.concatenated_data_headers_values[0] || [];
    const indicesHeaderColCount = indicesHeaders.length;
    const dataHeaderColCount = dataHeaders.length;

    writeHeaders(sheet, headers.value!);


    // 3. LOOP & POPULATE DATA
    let startRow = 2; // ExcelJS is 1-based, and Row 1 is headers

    for (const pair of pairs.value) {
      if (!pair._encrypted_shares || !Array.isArray(pair._encrypted_shares)) {
        throw new Error("pair.encrypted_shares is not array");
      }

      // Write Indices
      let currentCol = 1;
      for (const indicesRange of pair.indices) {
        writeMatrix(sheet, startRow, currentCol, indicesRange, false);
        currentCol += indicesRange.values[0]?.length || 0; // Move column forward

      }

      // Write Data
      currentCol = indicesHeaderColCount + 1;

      for (const dataRange of pair.data) {
        writeMatrix(sheet, startRow, currentCol, dataRange, true);
        currentCol += (dataRange.protectedValues?.[0]?.length || 0); // Move column forward

      }

      // Move startRow down for the next pair
      startRow += pair.data[0].values.length;
    }

    const metaSheet = workbook.addWorksheet("NERA_PROTECTION_METADATA");
    metaSheet.state = 'veryHidden'; // This hides it from the UI completely

    // Define the variables you need to save
    const indices_end_at = indicesHeaderColCount;
    const data_end_at = indicesHeaderColCount + dataHeaderColCount;

    // Create an array of Key-Value pairs
    const metaData = [
      ["nera_protocol_version", "v0.9.0"],
      ["indices_start_at", 1],
      ["indices_end_at", indices_end_at],
      ["data_start_at", indices_end_at + 1],
      ["data_end_at", data_end_at],
      ["data_owner_user", useUserStore().currentUser]
    ];

    // Write them to the hidden sheet (Column A = Key, Column B = Value)
    metaData.forEach((row, index) => {
      metaSheet.getCell(index + 1, 1).value = row[0]; // Key
      metaSheet.getCell(index + 1, 2).value = row[1]; // Value
    });
    // 5. CONVERT TO BASE64 & OPEN
    // Generate a Buffer
    const buffer = await workbook.xlsx.writeBuffer();

    // Convert Buffer to Base64 String
    const base64String = bufferToBase64(buffer);

    // Open the new workbook populated with data
    await Excel.createWorkbook(base64String);
  }

  // Helper to convert ArrayBuffer to Base64
  function bufferToBase64(buffer: ArrayBuffer): string {
    let binary = '';
    const bytes = new Uint8Array(buffer);
    const len = bytes.byteLength;
    for (let i = 0; i < len; i++) {
      binary += String.fromCharCode(bytes[i]);
    }
    return window.btoa(binary);
  }



  return { pairs, headers, nextId, addHeaders, clearPairs, clearHeaders, addPair, deletePair, createEncryptedDataSheet };
}
);