import { defineStore } from 'pinia';
import { ref } from 'vue';
import { generateAndPublishKeysForUser, writeSensitiveData } from "../../shared/apis";

export const useUserStore = defineStore('user', () => {
  // Read initial value from localStorage
  const currentUser = ref<string | null>(localStorage.getItem('currentUser-nera'));

  function login(username: string) {
    currentUser.value = username;
    // Call API with credentials, and API returns a valid Token and user_id. we use user_id as our user, and token when we need to auth.
    localStorage.setItem('currentUser-nera', username);
    generateAndPublishKeysForUser(username, onKeySuccess, onKeyFailure);
  }

  function logout() {
    currentUser.value = null;
    localStorage.removeItem('currentUser-nera');
    localStorage.removeItem('nera-keys-generated-at');
    localStorage.removeItem('nera-keys-uploaded-at');
  }

  function onKeySuccess(phase: string) {
    console.log(`- Phase ${phase} is success`)

    localStorage.setItem(`nera-keys-${phase}-at`, new Date().toISOString());
  }

  function onKeyFailure(phase: string) {
    console.log(`- Phase ${phase} is failed`)
  }

  return { currentUser, login, logout };
});